<template lang="en">
<div class="my-popups">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">

                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('DeployProductLocationWise') }}</h4>

                    <button type="button" class="close" v-on:click="ClosePopup">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>

                    <span class="user-guide ">
                        <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample"> <em class="fa fa-files-o pr-2"></em>User Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('useguideforRelatedInformation')"></div>
                            </div>
                        </div>
                    </span>

                </div>
                <div class="modal-body">
                    <Form ref="tagForm" autocomplete="off">
                        <div class="col-md-12">
                            <div class="form-group">
                                <div class="listing OuterMainDiv">
                                    <div class="row">
                                        <dynamic-form ref="AddDeployProduct" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmitDeploy">

                                            <template v-slot:tgslot-Location="{ data }">

                                                <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                                    <div class="input-group" v-if="isEdit == true">
                                                        <tree-view v-model="data.fieldInfo.value" :options="options" v-on:input="BindItemTypeByUserId(data.fieldInfo.value)" placeholder="Select Location" :disabled="treeViewDisabled" :show-count="true" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                        </tree-view>

                                                    </div>

                                                    <div class="input-group" v-else>
                                                        <tree-view v-model="data.fieldInfo.value" :options="options" v-on:input="BindItemTypeByUserId(data.fieldInfo.value)" placeholder="Select Location" :show-count="true" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                        </tree-view>

                                                    </div>

                                                    <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{ errors[0] }}</span>

                                                </Field>

                                            </template>
                                        </dynamic-form>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </Form>
                </div>

                <div class="modal-header theme-primary partition-full" v-if="ClickType == 'ADD'">
                    <h4 class="modal-title">{{ $t('Deploy List') }}</h4>
                </div>
                <div class="listing tblassestgroupinfodetails px-1 mt-3" v-if="ClickType == 'ADD'">
                    <div class="table-responsive ">
                        <table id="tblassestgroupinfodetails" class="table table-bordered dt-responsive nowrap mt-0">
                            <thead class="thead-bg">

                                <tr>
                                    <th class="text-center"><span>{{ $t('LOCATION') }}</span></th>
                                    <th class="text-center"><span>{{ $t('Quantity') }}</span></th>
                                    <th class="text-center"><span>{{ $t('UnitPrice') }}</span></th>
                                    <th><span>{{ $t('Model') }}</span></th>
                                    <th><span>{{ $t('Manufacturer') }}</span></th>
                                    <th><span>{{ $t('Vendor') }}</span></th>
                                    <th class="text-center"><span>{{ $t('ProductCostQty') }}</span></th>
                                    <th class="text-center"><span>{{ $t('RefPurchaseOrder') }}</span></th>
                                    <th class="text-center"><span>{{ $t('OrderDate') }} </span></th>
                                    <th><span>{{ $t('InvoiceNumber') }}</span></th>
                                    <th><span>{{ $t('InvoiceDate') }}</span></th>
                                    <th><span>{{ $t('NameOfInsurer') }}</span></th>
                                    <th><span>{{ $t('InsuranceReferenceNumber') }}</span></th>
                                    <th><span>{{ $t('InsuranceTo') }}</span></th>
                                    <th><span>{{ $t('WarrantyDuration') }}</span></th>
                                    <th><span>{{ $t('DepreciationRule') }}</span></th>
                                    <th><span>{{ $t('SalvageCost') }}</span></th>
                                    <th><span>{{ $t('ItemLife') }}</span></th>
                                    <th><span>{{ $t('Action') }}</span></th>
                                    <!-- <th><span>{{ $t('CalibrationFrequency') }}</span></th> -->

                                </tr>

                            </thead>

                            <tbody>
                                <tr v-for="(item, index) in arryaofData" :key="index">
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.LocationName }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.Quantity }}</span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.UnitPrice }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.Model }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.Manufacture }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.Vendor }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.ProductCostQty}}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.RefPurchaseOrder}}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">
                                            {{ $options.filters.formatDateTime(item.OrderDaTe) }}
                                        </span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.InvoiceNumber}}</span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">
                                            {{ $options.filters.formatDateTime(item.InvoiceDate)}}
                                        </span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{item.NameofInsurer}}</span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">{{item.InsuranceReferenceNumber}}</span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">
                                            {{ $options.filters.formatDateTime(item.InsuranceValidUpto) }}
                                        </span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">
                                            {{ $options.filters.formatDateTime(item.WarrantyDuration)}}
                                        </span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.DepreciationRule
                                                }}</span>
                                    </td>
                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.SalvageCost }}</span>
                                    </td>

                                    <td>
                                        <span class="text-overflow-dynamic-container">{{ item.ProductLifeInYears}}</span>
                                    </td>

                                    <td id="action_1" class="single-action text-center"><a href="javascript:;" class="deleteDataRow" title="Delete">
                                            <em class="fa fa-trash action-icon text-danger" v-on:click="DeleteRow(index)"></em>
                                        </a>
                                    </td>
                                </tr>

                                <tr v-if="noRecord == true">
                                    <td colspan="20" class="text-center text-danger no-record">{{
                                            $t('NoRecordfound') }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>

                    </div>
                </div>

                <div v-if="arryaofData.length > 0" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0 m-2">
                    <div class="row flex-row-reverse m-0">
                        <div class="col-lg-4 text-right">

                            <a href="javascript:;" v-on:click.once="SubmitData()" class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i class="fa fa-save pr-2"></i>
                                <span>{{ $t('Save') }}</span></a>
                            <a href="javascript:;" @click="ClosePopup()" :title="$t('Cancel')" class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{
                                        $t('Cancel') }}</span></a>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService';
// import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common'
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        'tree-view': Treeselect
    },
    props: {
        ClickType: {
            type: String
        },
        AsseCatalogtId: {
            type: String
        },
        DeployItemId: {
            type: String
        }
    },

    data() {
        return {

            arryaofData: [],
            LocationData: [],
            treeViewDisabled: true,
            options: [],
            isEdit: true,
            dData: [],
            FormSchema: [{
                layoutType: "double",
                Data: [{
                        astype: "SlotField",
                        label: this.$t('Location'),
                        name: "Location",
                        value: null,
                        placeholder: "Select Location",
                        disabled: true,
                        config: {
                            options: [],
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('DeploymentType'),
                        name: "DeployMentType",
                        value: "1",
                        placeholder: "",
                        config: {
                            options: [{
                                value: "1",
                                name: "Product"
                            }, {
                                value: "2",
                                name: "Inventory"
                            }],
                            onChange: this.OnDeplymentType,
                            //CallBackFunction: null
                        },

                        validationRules: {
                            "required": false
                        },

                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Quantity'),
                        name: "Quantity",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true,
                            max: 7,
                            "min_value": 1
                        },
                        config: {
                            onKeyUp: this.calcSum
                        }

                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Unit Price'),
                        name: "UnitPrice",
                        value: '',
                        placeholder: "",
                        validationRules: {
                            "required": true,
                            max: 5,
                            "min_value": 1
                        },
                        // onchange: this.calcSum
                        config: {
                            onKeyUp: this.calcSum
                        }
                    },

                    {
                        astype: "TextField",
                        label: this.$t('Model'),
                        name: "Model",
                        value: '',
                        placeholder: "",

                        // config: { format: "MM/DD/YYYY" }
                    },

                    {
                        astype: "TextField",
                        label: this.$t('Manufacture'),
                        name: "Manufacture",
                        value: '',
                        placeholder: "",

                    },

                    {
                        astype: "TextField",
                        label: this.$t('Vendor'),
                        name: "Vendor",
                        value: "",
                        placeholder: "",

                    },

                    {
                        astype: "NumericField",
                        label: this.$t('ProductCostQty'),
                        name: "ProductCostQty",
                        value: "",
                        placeholder: "",
                        disabled: true,

                        // validationRules: "required",

                    },

                    {
                        astype: "TextField",
                        label: this.$t('RefPurchaseOrder'),
                        name: "RefPurchaseOrder",
                        value: "",
                        placeholder: "",

                    },

                    {
                        astype: "DateField",
                        label: this.$t('OrderDate'),
                        name: "OrderDaTe",
                        value: '',
                        placeholder: "10/12/2022 12:AM",
                        config: {
                            //format: "YYYY-MM-DD",maxDate: (new Date())
                            format: "M/D/YYYY",
                            maxDate: (new Date())
                        }
                    },

                    {
                        astype: "TextField",
                        label: this.$t('InvoiceNumber'),
                        name: "InvoiceNumber",
                        value: "",
                        placeholder: "",

                    },

                    {
                        astype: "DateField",
                        label: this.$t('InvoiceDate'),
                        name: "InvoiceDate",
                        value: '',
                        placeholder: "10/12/2022 12:AM",
                        config: {
                            //format: "YYYY-MM-DD",maxDate: (new Date())
                            format: "M/D/YYYY",
                            maxDate: (new Date())
                        }

                    },

                    {
                        astype: "TextField",
                        label: this.$t('InsuranceReferenceNumber'),
                        name: "InsuranceReferenceNumber",
                        value: "",
                        placeholder: "",
                        disabled: true,

                    },

                    {
                        astype: "TextField",
                        label: this.$t('Name of Insurer'),
                        name: "NameofInsurer",
                        value: "",
                        placeholder: "",
                        disabled: true,

                    },

                    {
                        astype: "DateField",
                        label: this.$t('InsuranceValidUpto'),
                        name: "InsuranceValidUpto",
                        value: '',
                        placeholder: "10/12/2022 12:AM",
                        disabled: true,

                        config: {
                            format: "M/D/YYYY"
                        }
                    },

                    {
                        astype: "DateField",
                        label: this.$t('WarrantyDuration'),
                        name: "WarrantyDuration",
                        value: '',
                        placeholder: "10/12/2022 12:AM",
                        config: {
                            format: "M/D/YYYY",
                            minDate: (new Date())
                        }
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('DepreciationRule'),
                        name: "DepreciationRule",
                        value: "",
                        placeholder: "",
                        disabled: false,
                        config: {
                            options: [],

                        },

                    },

                    {
                        astype: "TextField",
                        label: this.$t('ProductLifeInYears'),
                        name: "ProductLifeInYears",
                        value: "",
                        placeholder: "",
                        disabled: false,

                    },

                    {
                        astype: "NumericField",
                        label: this.$t('Salvage Cost'),
                        name: "SalvageCost",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false,
                            max: 5,
                        },
                        config: {
                            onKeyUp: this.checkSalvageCost
                        },
                    },

                ]
            }],
            buttons: [{
                    type: "submit",
                    class: "btn btn-info",
                    text: "<i class='fa fa-plus pr-2'></i>Add To List",
                    isVisible: true
                },
                {
                    type: "button",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save ",
                    isVisible: false,
                    onClick: this.SubmitData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    isVisible: false,
                    onClick: this.ClosePopup
                }

            ],
            DataLoaded: 0,
            LocationName: '',
            noRecord: false,

        };
    },
    created: async function () {
        var vm = this;
        if (vm.ClickType == 'UPDATE') {
            vm.BindDataFileds();
            vm.buttons[0].isVisible = false;
            vm.buttons[1].isVisible = true;
            vm.buttons[2].isVisible = true;
            vm.isEdit = true;
        } else if (vm.ClickType == 'ADD') {
            vm.buttons[0].isVisible = true;
            vm.isEdit = false;
            vm.FormSchema[0].Data[12].disabled = false;
            vm.FormSchema[0].Data[13].disabled = false;
            vm.FormSchema[0].Data[14].disabled = false;
            if (vm.arryaofData.length <= 0) {
                vm.noRecord = true;
            }
        }
        await vm.GetLocation();
        await vm.GetDepreciationRule();
        // vm.DataLoaded = 1;
    },
    methods: {

        checkSalvageCost(field, data) {

            var vm = this;

            let unitPrice = parseInt(vm.FormSchema[0].Data[3].value);
            let salvageCost = parseInt(data.value.replace(/[^0-9]/g, ""));

            if (salvageCost >= unitPrice) {
                vm.ShowAlert(vm.$t("Salvage Cost must be less than Unit Price."), "warning", vm.$t("Alert"));
                return vm.FormSchema[0].Data[18].value = "";
            }

            return vm.FormSchema[0].Data[18].value = salvageCost.toString();
        },

        DeleteRow(index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ASSET_CATALOGUE_ID")), function () {
                vm.arryaofData.splice(index, 1)
            })

        },
        OnDeplymentType(event, field) {
            field.value = event.target.getAttribute('data-value');
        },
        ClosePopup() {

            var vm = this;
            //   vm.$parent.;
            vm.$parent.DeployClosePOp();

        },

        async GetLocation() {
            var vm = this;
            vm.isLoading = true;
            var params = `reqForm=`
            await DataService.GetLocationByuserId(params).then(response => {
                vm.LocationData = response.data.data;

                vm.FindalLocationArray = [];
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
                vm.options = vm.FindalLocationArray;
            })
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        calcSum(val) {
            var vm = this
            let ProductCost = 0;
            ProductCost = (vm.FormSchema)[0].Data[2].value * (vm.FormSchema)[0].Data[3].value;
            (vm.FormSchema)[0].Data[7].value = ProductCost;

        },

        onSubmitDeploy: function (formfields, val) {
            var vm = this;

            vm.noRecord = false;
            let LocationObj = vm.LocationData.filter((item) => {
                return item.COMPANY_LOCATION_ID == formfields.Location;
            });
            var DataObject = {
                LocationName: LocationObj[0].LOCATION_NAME,
                DeployMentType: formfields.DeployMentType,
                DepreciationRule: formfields.DepreciationRule,
                InsuranceReferenceNumber: formfields.InsuranceReferenceNumber,
                InsuranceValidUpto: formfields.InsuranceValidUpto,
                InvoiceDate: formfields.InvoiceDate,
                InvoiceNumber: formfields.InvoiceNumber,
                Location: formfields.Location,
                Manufacture: formfields.Manufacture,
                Model: formfields.Model,
                NameofInsurer: formfields.NameofInsurer,
                OrderDaTe: formfields.OrderDaTe,
                ProductCostQty: formfields.ProductCostQty,
                ProductLifeInYears: formfields.ProductLifeInYears,
                Quantity: formfields.Quantity,
                RefPurchaseOrder: formfields.RefPurchaseOrder,
                SalvageCost: formfields.SalvageCost,
                UnitPrice: formfields.UnitPrice,
                Vendor: formfields.Vendor,
                WarrantyDuration: formfields.WarrantyDuration,
            }
            vm.arryaofData.push(DataObject);

            vm.LocationName = LocationObj[0].LOCATION_NAME;
            (vm.FormSchema)[0].Data[0].value = null;
            (vm.FormSchema)[0].Data[2].value = 0;
            (vm.FormSchema)[0].Data[3].value = 0;
            (vm.FormSchema)[0].Data[4].value = "";
            (vm.FormSchema)[0].Data[5].value = "";
            (vm.FormSchema)[0].Data[6].value = "";
            (vm.FormSchema)[0].Data[7].value = 0;
            (vm.FormSchema)[0].Data[8].value = "";
            (vm.FormSchema)[0].Data[9].value = null;
            (vm.FormSchema)[0].Data[10].value = "";
            (vm.FormSchema)[0].Data[11].value = null;
            (vm.FormSchema)[0].Data[15].value = null;
            (vm.FormSchema)[0].Data[16].value = null;
            (vm.FormSchema)[0].Data[17].value = null;
            (vm.FormSchema)[0].Data[18].value = null;

        },

        // resetinput() {
        //     this.Quantity = "";
        // },

        async SubmitData() {
            var vm = this;
            var ArrayData = [];
            let myObj = {}
            if (vm.arryaofData.length > 0) {
                vm.arryaofData.forEach((item, index) => {
                    myObj = {
                        DeployId: vm.ClickType == "UPDATE" ? vm.DeployItemId : 0,
                        AssetCatalogueId: parseInt(vm.$route.params.id),
                        LocationId: item.Location, //(vm.FormSchema)[0].Data[0].value,
                        Quantity: item.Quantity, //(vm.FormSchema)[0].Data[2].value,
                        UnitPrice: item.UnitPrice, //(vm.FormSchema)[0].Data[3].value,
                        Model: item.Model, //(vm.FormSchema)[0].Data[4].value,
                        Manufacturer: item.Manufacture, //(vm.FormSchema)[0].Data[5].value,
                        Vendor: item.Vendor,
                        PurchaseOrder: item.RefPurchaseOrder, //(vm.FormSchema)[0].Data[8].value,
                        WarrantyDuration: item.WarrantyDuration, //(vm.FormSchema)[0].Data[15].value,
                        DepreciationRuleId: item.depreciation_rule,
                        OrderDate: item.OrderDaTe, //(vm.FormSchema)[0].Data[9].value,
                        AssetCost: item.ProductCostQty, //(vm.FormSchema)[0].Data[7].value,
                        SalvageCost: item.SalvageCost, //(vm.FormSchema)[0].Data[18].value,
                        ItemLife: null,
                        InvoiceNumber: item.InvoiceNumber, //(vm.FormSchema)[0].Data[10].value,
                        InvoiceDate: item.InvoiceDate, //(vm.FormSchema)[0].Data[11].value,
                        InsurerName: item.NameofInsurer,
                        InsuranceRefNumber: item.InsuranceReferenceNumber,
                        InsuranceTo: item.InsuranceValidUpto,
                        DeploymentType: item.DeployMentType, //(vm.FormSchema)[0].Data[1].value,
                    }
                    ArrayData.push(myObj);
                });
            } else {
                myObj = {

                    AssetCatalogueId: parseInt(vm.$route.params.id),
                    LocationId: (vm.FormSchema)[0].Data[0].value,
                    DeploymentType: (vm.FormSchema)[0].Data[1].value,
                    DeployId: vm.ClickType == "UPDATE" ? vm.DeployItemId : 0,
                    Quantity: (vm.FormSchema)[0].Data[2].value,
                    UnitPrice: (vm.FormSchema)[0].Data[3].value,
                    Model: (vm.FormSchema)[0].Data[4].value,
                    Manufacturer: (vm.FormSchema)[0].Data[5].value,
                    Vendor: (vm.FormSchema)[0].Data[6].value,
                    AssetCost: (vm.FormSchema)[0].Data[7].value,
                    PurchaseOrder: (vm.FormSchema)[0].Data[8].value,
                    OrderDate: (vm.FormSchema)[0].Data[9].value,
                    InvoiceNumber: (vm.FormSchema)[0].Data[10].value,
                    InvoiceDate: (vm.FormSchema)[0].Data[11].value,
                    InsuranceRefNumber: (vm.FormSchema)[0].Data[12].value,
                    InsurerName: (vm.FormSchema)[0].Data[13].value,
                    InsuranceTo: (vm.FormSchema)[0].Data[14].value,
                    WarrantyDuration: (vm.FormSchema)[0].Data[15].value,
                    DepreciationRuleId: vm.FormSchema[0].Data[16].value,
                    ItemLife: (vm.FormSchema)[0].Data[17].value,
                    SalvageCost: (vm.FormSchema)[0].Data[18].value,
                }
                ArrayData.push(myObj);
            }
            if (myObj.UnitPrice <= parseFloat(myObj.SalvageCost)) {
                return vm.ShowAlert(vm.$t("Salvage Cost must be less than Unit Price."), "warning", vm.$t("Alert"))
            }
            //var JsonObj = JSON.stringify(ArrayData);
            await DataService.SaveDeploy(ArrayData).then(function (response) {

                if (response.data.msg == 'DeployedSuccessfully') {
                    vm.ClosePopup();
                    vm.$parent.FetchData();
                    vm.ShowAlert(vm.$t('ProductDeployAdd'), "success", true, vm.$t("Alert"))
                } else if (response.data.msg = "DeployItem") {
                    vm.ClosePopup();
                    vm.$parent.FetchData();
                    vm.ShowAlert(vm.$t('ProductDeployUpdated'), "success", true, vm.$t("Alert"))
                } else {
                    vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                }

            })

        },
        async BindDataFileds() {
            var vm = this;
            var url = `DeployItemId=${vm.DeployItemId}&AssetCatalogId=${this.$route.params.id}`;
            await DataService.GetDeployItems(url).then(response => {

                if (JSON.parse(response.data.result) != null) {
                    var fieldDataValues = JSON.parse(response.data.result);
                    if (JSON.parse(response.data.result).length > 0) {
                        vm.FormSchema[0].Data[0].value = fieldDataValues[0].location;
                        vm.FormSchema[0].Data[0].disabled = true;
                        vm.FormSchema[0].Data[2].disabled = true;
                        vm.FormSchema[0].Data[1].value = fieldDataValues[0].deployement_type == "1" ? '1' : '2';;
                        vm.FormSchema[0].Data[2].value = fieldDataValues[0].quantity;
                        vm.FormSchema[0].Data[3].value = fieldDataValues[0].unit_price;
                        vm.FormSchema[0].Data[4].value = fieldDataValues[0].model;
                        vm.FormSchema[0].Data[5].value = fieldDataValues[0].manufacturer;
                        vm.FormSchema[0].Data[6].value = fieldDataValues[0].vendor;
                        vm.FormSchema[0].Data[7].value = fieldDataValues[0].asset_cost;
                        vm.FormSchema[0].Data[8].value = fieldDataValues[0].purchase_order;
                        vm.FormSchema[0].Data[9].value = fieldDataValues[0].order_date;
                        vm.FormSchema[0].Data[10].value = fieldDataValues[0].invoice_number;
                        vm.FormSchema[0].Data[11].value = fieldDataValues[0].invoice_date;
                        vm.FormSchema[0].Data[12].value = fieldDataValues[0].insurance_ref_number;
                        vm.FormSchema[0].Data[13].value = fieldDataValues[0].insurer_name;
                        vm.FormSchema[0].Data[14].value = fieldDataValues[0].insurance_valid_upto;
                        vm.FormSchema[0].Data[15].value = fieldDataValues[0].warranty_duration;
                        vm.FormSchema[0].Data[16].value = fieldDataValues[0].depreciation_rule;
                        vm.FormSchema[0].Data[17].value = fieldDataValues[0].item_life;
                        vm.FormSchema[0].Data[18].value = fieldDataValues[0].salvage_cost;
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });

        },
        async GetDepreciationRule() {
            var vm = this;
            vm.isLoading = true;
            vm.DepreciationData = [];
            var obj = {};
            await DataService.GetDepreciationRuleList().then(response => {
                if (typeof response.data === 'string') {
                    vm.DepreciationData = JSON.parse(response.data);
                } else {
                    vm.DepreciationData = response.data;
                }
                //vm.DepreciationData = JSON.parse(response.data);
                vm.DepreciationData.forEach((item, index) => {
                    obj = {
                        name: item.DEPRECIATION_RULE_NAME,
                        value: item.DEPRECIATION_RULE_ID
                    }
                    vm.dData.push(obj);
                });
                (vm.FormSchema)[0].Data[16].config.options = vm.dData;
                vm.$refs.AddDeployProduct.UpdateKeyValue();

            });
            vm.isLoading = false;
        },
    },

};
</script>

